@use 'base/variables' as v;

.alert{
    padding: 1rem 1rem 1rem 2rem;
    text-transform: uppercase;
    font-weight: 900;
    margin: 1rem 0;
    font-size: 1.3rem;
    max-width: 80rem;

    &.error{
        background-color: lighten($color: v.$red, $amount: 60%);
        border-left: .5rem solid v.$red;
        color: v.$red;
    }

    &.exito{
        background-color: lighten($color: v.$green, $amount: 60%);
        border-left: .5rem solid v.$green;
        color: v.$green;
    }
}