html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 70rem;
  margin: 0 auto;
}

.container-projects {
  width: 95%;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.page-description {
  text-align: center;
}

.form {
  margin-top: 5rem;
}
.form .field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.form .field label {
  flex: 0 0 10rem;
}
.form .field input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}
.form .field2 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 2rem;
}
.form .field2 p {
  font-size: 1.6rem;
  color: black;
  margin-right: 4rem;
  cursor: default;
}

.actions {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .actions {
    display: flex;
    justify-content: space-between;
  }
}
.actions a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.actions a:last-of-type {
  margin-bottom: 0;
}

.alert {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.3rem;
  max-width: 80rem;
}
.alert.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alert.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.login {
  margin-top: 5rem;
}
.login .projectbooster {
  background: linear-gradient(to right, #000 0%, #d0d3da 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-weight: bold;
}
.login input[type=submit] {
  background-color: #000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: black;
  cursor: pointer;
}
.login .actions a {
  color: #000;
}

.login-img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cont-img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cont-img img {
  width: 50rem;
}

.btn-1 {
  display: grid;
  place-content: center;
}
.btn-1 a {
  font-size: 2rem;
  font-weight: bold;
  color: #000;
}

.btn-inc {
  background-color: #000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .btn-inc {
    width: auto;
  }
}
.btn-inc:hover {
  background-color: black;
  cursor: pointer;
}

.cont-btn {
  width: 40%;
}

.p-message {
  color: #000;
}

.header {
  display: flex;
  flex-direction: column-reverse;
  background: linear-gradient(0deg, rgba(22, 83, 71, 0.801) 0%, rgba(35, 43, 45, 0.904) 100%), url(../img/background.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 5px 5px 15px 5px rgba(35, 43, 45, 0.6);
  width: 100%;
  height: 60rem;
}
@media (min-width: 480px) {
  .header {
    display: block;
    background: linear-gradient(0deg, rgba(26, 100, 85, 0.801) 0%, rgba(35, 43, 45, 0.904) 100%), url(../img/background.webp);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
@media (min-width: 480px) {
  .header {
    height: 50rem;
  }
}
.header .nav {
  padding: 4rem;
  display: flex;
  gap: 2rem;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 480px) {
  .header .nav {
    flex-direction: row;
  }
}
.header .nav a {
  color: #FFFFFF;
}
.header .nav a:hover {
  color: #d9d9d9;
}
.header .nav nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 480px) {
  .header .nav nav {
    flex-direction: row;
    gap: 4rem;
  }
}
.header .nav .sesion-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  gap: 2rem;
}
@media (min-width: 480px) {
  .header .nav .sesion-nav {
    flex-direction: row;
    gap: 4rem;
  }
}

.header-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (min-width: 480px) {
  .header-center {
    margin-top: 4rem;
  }
}
.header-center h1 {
  color: #FFFFFF;
  margin: 0;
}
.header-center p {
  text-align: center;
  color: rgba(255, 255, 255, 0.768627451);
}
@media (min-width: 768px) {
  .header-center p {
    width: 70rem;
  }
}

.main {
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .main {
    margin-top: 8rem;
  }
}
@media (min-width: 1024px) {
  .main {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "col1 col2";
  }
}

.contact {
  background-color: #000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .contact {
    width: auto;
  }
}
.contact:hover {
  background-color: black;
  cursor: pointer;
}

.cont-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.cont-content h2 {
  text-align: center;
}
@media (min-width: 768px) {
  .cont-content h2 {
    text-align: left;
  }
}
.cont-content p {
  color: #1a1a1a;
}

.div-main {
  width: 100%;
  display: flex;
}

.cont-img {
  display: none;
  justify-content: center;
  align-items: center;
  grid-area: col2;
}
.cont-img img {
  width: 70%;
  border-radius: 4rem;
}
@media (min-width: 1024px) {
  .cont-img {
    display: flex;
  }
}

.video-contain {
  display: grid;
  grid-template-columns: 20% 80%;
  align-items: center;
}
.video-contain span {
  color: #000;
  font-weight: bold;
}
.video-contain:hover span {
  color: #1a1a1a;
}

.parts {
  margin-top: 6rem;
  text-align: center;
}

.animated {
  opacity: 0;
  transform: translateY(5rem);
  animation: entradaMain 1s forwards;
}

@keyframes entradaMain {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.bg-middle {
  display: grid;
  place-items: center;
  width: 6rem;
  height: 16rem;
  border-radius: 2rem;
  padding: 1rem;
}

.middle {
  display: flex;
  flex-direction: column;
  gap: 8rem;
  margin-top: 6rem;
}
@media (min-width: 768px) {
  .middle {
    display: grid;
    grid-template-columns: 50% 50%;
  }
}
@media (min-width: 1024px) {
  .middle {
    display: grid;
    grid-template-columns: 20% 20% 20%;
  }
}

.grid-middle {
  display: grid;
  grid-template-columns: 10% 80%;
  align-items: center;
  gap: 4rem;
}
@media (min-width: 1024px) {
  .grid-middle {
    grid-template-columns: 20% 70%;
  }
}

.op-text {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.op-text div {
  background-color: #484d56;
  width: 18rem;
  height: 0.5rem;
}
.op-text span {
  color: #484d56;
  font-size: 3rem;
  font-weight: bold;
}
.op-text p {
  font-size: 1.2rem;
  margin: 0;
  font-weight: bold;
}

.article-wrapper {
  transition: 0.15s all ease-in-out;
  border-radius: 10px;
  padding: 5px;
  border: 4px solid rgba(46, 179, 152, 0.5215686275);
  cursor: pointer;
  background-color: white;
  box-shadow: 4px 4px 0 #238673, 8px 8px 0 #155246;
}

.article-wrapper:hover {
  box-shadow: 10px 10px 0 #238673, 20px 20px 0 #155246;
  border-color: #2eb398;
  background-color: #f2f2f2;
  transform: translate(-20px, -20px);
}

.article-wrapper:active {
  box-shadow: none;
  transform: translate(0, 0);
}

.types {
  gap: 10px;
  display: flex;
  place-content: flex-start;
}

.rounded-lg {
  border-radius: 10px;
}

.article-wrapper:hover .project-hover {
  transform: rotate(-30deg);
}

.project-info {
  padding-top: 20px;
  padding: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.project-title {
  font-size: 1.4em;
  margin: 0;
  font-weight: 600;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: black;
}
@media (min-width: 768px) {
  .project-title {
    font-size: 1.8em;
  }
}

.flex-pr {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-type {
  background: #5ed6be;
  color: #369481;
  font-weight: bold;
  padding: 0.3em 0.7em;
  border-radius: 15px;
  font-size: 12px;
  letter-spacing: -0.6px;
}

.project-hover {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  padding: 9px;
  transition: all 0.3s ease;
}

.container-project {
  width: 100%;
  height: 170px;
  background: gray;
}

.cssbuttons-io-button {
  background: #248a76;
  box-shadow: 5px 5px 15px 5px #232b2d;
  color: #FFFFFF;
  padding: 0.5em;
  padding-left: 1.2em;
  font-weight: bold;
  border-radius: 0.9em;
  border: none;
  letter-spacing: 0.05em;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
  height: 2.8em;
  padding-right: 3.3em;
}

.cssbuttons-io-button .icon {
  background: #FFFFFF;
  margin-left: 1em;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.2em;
  width: 2.2em;
  border-radius: 0.7em;
  right: 0.3em;
  transition: all 0.3s;
}

.cssbuttons-io-button:hover .icon {
  width: calc(100% - 0.6em);
}

.cssbuttons-io-button .icon svg {
  width: 1.1em;
  transition: transform 0.3s;
  color: #000;
}

.cssbuttons-io-button:hover .icon svg {
  transform: translateX(0.1em);
}

.cssbuttons-io-button:active .icon {
  transform: scale(0.95);
}

.sesion a {
  align-items: center;
  background-color: #f2f2f2;
  color: black;
  cursor: pointer;
  display: flex;
  font-weight: 700;
  line-height: 1.5;
  text-decoration: none;
  text-transform: uppercase;
  outline: 0;
  border: 2.5px solid rgba(32, 32, 32, 0.5215686275);
  padding: 1rem;
}
.sesion a:before {
  background-color: black;
  content: "";
  display: inline-block;
  height: 1px;
  margin-right: 10px;
  transition: all 0.42s cubic-bezier(0.25, 0.8, 0.25, 1);
  width: 1rem;
}
.sesion a:hover:before {
  background-color: black;
  width: 3rem;
}

.container-new-project {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}
.container-new-project .checkbox-tile-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.5rem;
}
.container-new-project .checkbox-tile-group .input-container {
  position: relative;
  height: 50px;
  width: 100px;
  margin: 0.5rem;
}
.container-new-project .checkbox-tile-group .input-container .checkbox-button {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  margin: 0;
  cursor: pointer;
}
.container-new-project .checkbox-tile-group .input-container .checkbox-tile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border: 2px solid #26937c;
  border-radius: 5px;
  padding: 1rem;
  transition: transform 300ms ease;
}
.container-new-project .checkbox-tile-group .input-container .checkbox-tile .icon svg {
  fill: #26937c;
  width: 2rem;
  height: 2rem;
}
.container-new-project .checkbox-tile-group .input-container .checkbox-tile .checkbox-tile-label {
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #26937c;
}
.container-new-project .checkbox-tile-group .input-container .checkbox-tile .checkbox-tile-label:hover {
  cursor: pointer;
}
.container-new-project .checkbox-tile-group .input-container .checkbox-tile .checkbox-tile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  border: 2px solid #26937c;
  border-radius: 5px;
  padding: 1rem;
  transition: transform 300ms ease;
}
.container-new-project .checkbox-tile-group .input-container .checkbox-button:checked + .checkbox-tile {
  background-color: #26937c;
  border: 2px solid #26937c;
  color: white;
  transform: scale(1.1, 1.1);
}
.container-new-project .checkbox-tile-group .input-container .checkbox-button:checked + .checkbox-tile .icon svg {
  fill: white;
  background-color: #26937c;
